@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/gilroy-regular.ttf);
}
@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/gilroy-medium.ttf);
}
@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 600;
  src: url(../fonts/gilroy-semibold.ttf);
}
@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 700;
  src: url(../fonts/gilroy-bold.ttf);
}
@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 800;
  src: url(../fonts/Gilroy-extrabold.otf);
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  font-family: "gilroy", "Helvetica", sans-serif;
  font-style: normal;
}

a {
  text-decoration: none;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}

main {
  flex: 1 1 auto;
}

.header {
  width: 100%;
}
.header__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.logo__img {
  padding-top: 33px;
}

.menu__list {
  display: flex;
  list-style: none;
  padding-top: 39px;
}
.menu__list-items {
  margin-left: 72px;
  color: #2C2F36;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}

.menu__buttons {
  margin-top: 24px;
}

.menu__list-button {
  min-width: 256px;
  min-height: 48px;
  background-color: #FB8C00;
  border-radius: 88px;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
  line-height: 140%;
  border: none;
  position: relative;
  padding-left: 14px;
}

.menu__list-button::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 14px;
  background-image: url(../../img/main/calculate-button.svg);
  left: 25px;
  top: 15px;
}

.top-section {
  max-width: 1600px;
  margin: 0 auto;
  padding-top: 109px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.top-section__container {
  margin-left: 200px;
}

.top-section::before {
  position: absolute;
  content: "";
  width: 1600px;
  height: 940px;
  top: 0;
  background-image: url(../../img/main/background-color-header.jpg);
  background-repeat: no-repeat;
  object-fit: contain;
  background-position: center;
  z-index: -3;
}

@media (min-width: 375px) and (max-width: 765px) {
  .top-section {
    padding-top: 40px;
    max-width: 375px;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
  }
  .top-section__container {
    margin-left: 0px;
    padding-top: 73px;
  }
}
.top-section-title {
  font-weight: 800;
  font-size: 72px;
  line-height: 88px;
  color: #2C2F36;
  width: 370px;
}
.top-section-title-color {
  color: #FB8C00;
}

@media (min-width: 375px) and (max-width: 765px) {
  .top-section-title {
    max-width: 319px;
    max-height: 152px;
    font-size: 62px;
    line-height: 76px;
  }
}
.paragraph-top {
  display: flex;
  justify-content: left;
}
.paragraph-top__text {
  margin-left: 63px;
  position: relative;
  margin-top: 199px;
}
.paragraph-top__text:nth-child(1) {
  margin-left: 0;
  max-width: 162px;
}
.paragraph-top__text:nth-child(2) {
  max-width: 195px;
}
.paragraph-top__text:nth-child(3) {
  max-width: 186px;
}

.paragraph-top__text::before {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  background-color: #fff;
  background-position: center;
  background-image: url(../../img/main/layer.svg);
  background-repeat: no-repeat;
  border-radius: 50px;
  top: -84px;
}

.paragraph-top__text:nth-child(1)::before {
  background-image: url(../../img/main/pencil.svg);
}

.paragraph-top__text:nth-child(3)::before {
  background-image: url(../../img/main/printer.svg);
}

.paragraph-top__text:nth-child(1)::after,
.paragraph-top__text:nth-child(2)::after {
  position: absolute;
  content: "";
  width: 3px;
  height: 150px;
  background-color: #9BA9B5;
  opacity: 0.2;
  border-radius: 36px;
  top: -95px;
  right: -30px;
}

@media (min-width: 375px) and (max-width: 765px) {
  .paragraph-top {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .paragraph-top__text {
    margin-left: 0;
    position: relative;
  }
  .paragraph-top__text:nth-child(1) {
    max-width: 345px;
  }
  .paragraph-top__text:nth-child(2) {
    max-width: 345px;
  }
  .paragraph-top__text:nth-child(3) {
    max-width: 276px;
  }

  .paragraph-top__text:nth-child(1)::after,
.paragraph-top__text:nth-child(2)::after {
    position: absolute;
    content: "";
    width: 150px;
    height: 3px;
    background-color: #9BA9B5;
    opacity: 0.2;
    border-radius: 36px;
    top: 64px;
    left: 0px;
  }
}
.section-printer__image {
  position: relative;
  top: -40px;
}

@media (min-width: 375px) and (max-width: 765px) {
  .section-printer__image {
    max-width: 385px;
    max-height: 236px;
    object-fit: contain;
  }
}
.details {
  padding-top: 307px;
  max-width: 1200px;
  margin: 0 auto;
}
.details__elements {
  padding-top: 129px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  align-items: end;
  justify-items: center;
  text-align: center;
  gap: 126px 30px;
}

.details-title {
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  color: #2C2F36;
}

.element-items {
  min-height: 284px;
  min-width: 370px;
  background: radial-gradient(98.97% 276.64% at 1.03% 40.7%, #EBF6FF 0%, #EFF8FF 100%);
  border-radius: 35px;
}

.element-items__title {
  max-width: 215px;
  padding-left: 78px;
  margin-bottom: 16px;
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
}

.element-items__text {
  max-width: 228px;
  padding-left: 71px;
  padding-bottom: 40px;
}

.element-items:nth-child(7) > .element-items__title {
  max-width: 230px;
}

.element-items:nth-child(8) > .element-items__text {
  max-width: 262px;
  padding-left: 54px;
}

.element-items:nth-child(6) > .element-items__title {
  max-width: 259px;
  padding-left: 56px;
}

.element-items-img {
  height: 200px;
}

.element-items__image {
  transform: translateY(-40px);
}

.element-items:nth-child(7) > .element-items-img {
  height: 175px;
  transform: translateY(40px);
}

.element-items:nth-child(1) > .element-items-img {
  height: 175px;
}

.condition-work {
  max-width: 1430px;
  padding-top: 194px;
}

@media (min-width: 1601px) {
  .condition-work {
    margin: 0 auto;
  }
}
.condition-work-title {
  padding-left: 215px;
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  color: #2C2F36;
}

@media (min-width: 1601px) {
  .condition-work-title {
    padding-left: 120px;
  }
}
.condition-work-container {
  padding-top: 100px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.services {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.2fr 0.3fr 0.3fr;
  row-gap: 30px;
  justify-items: end;
}

.services__info {
  box-shadow: 0px 4px 68px rgba(32, 52, 91, 0.08);
  border-radius: 35px;
  max-width: 271px;
  min-height: 151px;
  position: relative;
  overflow: hidden;
}
.services__info-text {
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #2C2F36;
}

.services__info::before {
  position: absolute;
  content: "";
  width: 168px;
  height: 168px;
  background-image: url(../../img/main/services-work-info.svg);
  top: -59px;
  left: -59px;
}

.services__info-number {
  position: absolute;
  top: 25px;
  left: 25px;
  font-weight: bold;
  font-size: 36px;
  line-height: 140%;
  color: #2F80ED;
}

.services__info:nth-child(1) {
  grid-column: 1/3;
  max-width: 570px;
}

.services__info-text__first,
.services__info-text__second,
.services__info-text__fourth {
  padding-top: 51px;
  padding-bottom: 50px;
  padding-right: 25px;
  padding-left: 118px;
}

.services__info-text__third,
.services__info-text__fifth {
  padding-top: 38px;
  padding-left: 118px;
  padding-right: 25px;
  padding-bottom: 38px;
}

.services__info-text__fifth {
  max-width: 136px;
  padding-right: 19px;
  padding-left: 116px;
}

.cost {
  width: 100%;
  margin: 0 auto;
  background-image: radial-gradient(100.93% 242.98% at 1.19% 19.9%, #EBF6FF 0%, #EBEAF8 33.33%, #D8EDEE 61.98%, #EFF4F9 82.29%, #EFF8FF 100%), url(../../img/main/background-cost.png);
  margin-bottom: 180px;
}

.cost-wrapper {
  max-width: 1140px;
  margin: 0 auto;
}

.cost-title {
  padding-top: 200px;
  padding-bottom: 64px;
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  color: #2C2F36;
}
.cost-title__color {
  color: #FB8C00;
}

.cost-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.materials {
  display: flex;
  justify-content: space-between;
  max-width: 470px;
  min-height: 635px;
  background: #F1F5F8;
  box-shadow: 0px 4px 68px rgba(33, 33, 33, 0.06);
  border-radius: 35px;
}
.materials__button {
  margin-top: 44px;
  margin-left: 40px;
  background: none;
  border: none;
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
}

.materials__list,
.materials__list-active {
  list-style: none;
  margin: 0;
  padding: 0;
}

.materials__button {
  position: relative;
}

.button-material {
  color: #FB8C00;
}

.button-material::before {
  position: absolute;
  content: "";
  width: 101px;
  height: 3px;
  background-color: #FB8C00;
  top: 28px;
}

.button-printers {
  color: #2C2F36;
  opacity: 0.4;
}

.materials__list {
  margin-top: 48px;
  padding-left: 40px;
}
.materials__list-element {
  margin-bottom: 29px;
}
.materials__list-link {
  color: #2C2F36;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  position: relative;
}

.materials__list-link::after {
  position: absolute;
  content: "";
  width: 390px;
  height: 3px;
  background-color: #B4BBCE;
  opacity: 0.3;
  top: 32px;
  left: 0;
}

.materials__list-descripton {
  display: none;
}

.materials__list-active {
  padding-top: 115px;
}
.materials__list-active-element {
  margin-bottom: 26px;
  padding-right: 48px;
}

.materials__list-button {
  width: 11px;
  height: 11px;
  font-size: 22px;
}

.photo-model {
  margin-left: 30px;
  background: #FFFFFF;
  box-shadow: 0px 4px 68px rgba(33, 33, 33, 0.06);
  border-radius: 35px;
  width: 670px;
  max-height: 635px;
}
.photo-model__scroll {
  padding-top: 32px;
  padding-bottom: 40px;
  display: flex;
  margin-left: 20px;
}
.photo-model__scroll-step {
  width: 183px;
  height: 4px;
  background-color: #FB8C00;
  border-radius: 38px;
  margin-left: 20px;
}

.photo-model__scroll-step:nth-child(2),
.photo-model__scroll-step:nth-child(3) {
  background: #B4BBCE;
  opacity: 0.3;
}

.photo-model__button-back,
.photo-model__button-next {
  min-width: 160px;
  min-height: 50px;
  border-radius: 37px;
  border: none;
  font-weight: bold;
  font-size: 16px;
  line-height: 140%;
  color: #fff;
}

.photo-model__button-back {
  background-color: #B4BBCE;
}

.photo-model__button-next {
  background-color: #FB8C00;
}

.photo-model-title {
  font-weight: 800;
  font-size: 24px;
  line-height: 29px;
  color: #2C2F36;
  padding-top: 44px;
  padding-left: 40px;
}

.container-photo {
  position: relative;
  box-sizing: border-box;
  width: 590px;
  height: 109px;
  margin-left: 40px;
  border-radius: 7px;
  padding-top: 40px;
  border: dashed #B4BBCE;
  font-weight: bold;
  font-size: 16px;
  line-height: 140%;
  color: #2C2F36;
  text-align: center;
  cursor: pointer;
}
.container-photo__file-upload {
  outline: 0;
  opacity: 0;
  pointer-events: none;
  user-select: none;
}
.container-photo__icon {
  margin-right: 13px;
}

.container-photo:hover {
  border: 3px dashed #FB8C00;
}

.custom-file-upload {
  width: 590px;
  height: 109px;
}

.permission {
  width: 100%;
  font-weight: 500;
  font-size: 12px;
  line-height: 140%;
  color: #959CAC;
}
.permission__format {
  display: flex;
  max-width: 590px;
  padding-left: 40px;
  padding-top: 16px;
  justify-content: space-between;
}
.permission__file-size {
  max-width: 324px;
  padding-left: 40px;
  display: flex;
  justify-content: space-between;
}

.permission__format-text:nth-child(2) {
  padding-left: 144px;
  max-width: 298px;
}

.permission__file-size-text {
  padding-top: 16px;
}

.permission__button {
  display: flex;
  justify-content: space-between;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 60px;
}

.link-cloud {
  width: 590px;
  height: 70px;
  background: #F9FAFB;
  border: 1px solid #ECEEF1;
  box-sizing: border-box;
  border-radius: 7px;
  margin-left: 40px;
  margin-top: 32px;
  padding-left: 24px;
  padding-bottom: 35px;
}

.section-questions {
  width: 1170px;
  height: 433px;
  margin: 0 auto;
  margin-bottom: 180px;
  background-image: radial-gradient(98.97% 276.64% at 1.03% 40.7%, #EBF6FF 0%, #EFF8FF 100%);
  border-radius: 35px;
}
.section-questions__wrapper {
  display: flex;
}

.questions-title {
  padding-top: 70px;
  padding-left: 70px;
  padding-bottom: 24px;
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  color: #2C2F36;
}

.section-questions__paragraph {
  max-width: 380px;
  padding-left: 70px;
}

.reverse-form {
  padding-top: 90px;
  margin-left: 73px;
  display: grid;
  grid-template-columns: 250px 300px;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 0 20px;
}

.reverse-form__input {
  box-sizing: border-box;
  width: 235px;
  height: 50px;
  border-radius: 7px;
  border: none;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 24px;
}

.reverse-form__input:nth-child(3) {
  display: block;
  width: 500px;
  grid-column: 1/3;
  width: 510px;
  min-height: 100px;
}

.reverse-form__send {
  margin-top: 32px;
  width: 235px;
  height: 50px;
  border: none;
  background-color: #FB8C00;
  border-radius: 37px;
  font-weight: bold;
  font-size: 16px;
  line-height: 140%;
  color: #fff;
}

.questions_politic {
  padding-right: 57px;
  margin-top: 38px;
  height: 34px;
  font-weight: 500;
  font-size: 12px;
  line-height: 140%;
  color: #959CAC;
}
.questions_politic-link {
  color: #2F80ED;
}

.footer {
  width: 100%;
  min-height: 415px;
}
.footer__wrapper {
  background-image: url(../../img/main/background-footer.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: 180px;
  padding-left: 200px;
  padding-right: 200px;
  margin: 0 auto;
  min-height: 250px;
  max-width: 1600px;
  display: grid;
  align-self: center;
  grid-template-columns: 0.5fr 1fr 3fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: "footer-logo footer-menu footer__data" "footer-political footer-political footer-political";
}
.footer__address {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #2C2F36;
  padding-bottom: 12px;
}

.footer-logo {
  grid-area: footer-logo;
  padding-top: 10px;
}

.footer-menu {
  grid-area: footer-menu;
  align-self: center;
}

.footer__data {
  grid-area: footer__data;
  justify-self: end;
  align-self: center;
  display: flex;
}
.footer__data-telefon {
  margin-right: 89px;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  color: #2C2F36;
}
.footer__data-contact {
  padding-top: 5px;
  text-align: right;
}

.footer__social-media {
  display: flex;
  justify-content: end;
}
.footer__social-media-facebook, .footer__social-media-youtube {
  margin-top: 18px;
  margin-right: 24px;
  background-color: #B4BBCE;
  width: 44px;
  height: 44px;
  border-radius: 30px;
  background-image: url(../../img/main/youtube.svg);
  background-repeat: no-repeat;
  background-position: center;
}
.footer__social-media-youtube {
  background-image: url(../../img/main/facebook.svg);
  margin-right: 0;
}

.footer-menu__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer-menu__list-items {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
}

.footer-political {
  grid-area: footer-political;
  position: relative;
  margin-top: 118px;
  padding-bottom: 27px;
  display: flex;
  justify-content: space-between;
}
.footer-political__links {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #B4BBCE;
  position: relative;
}

.footer-political__links:last-child {
  padding-right: 17px;
}

.footer-political__links:last-child::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-image: url(../../img/main/heart.svg);
  right: 0px;
  top: 3px;
}

.footer-political::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 4px;
  background-color: #B4BBCE;
  opacity: 0.3;
  border-radius: 38px;
  top: -27px;
}